.tf-page-title{padding:24px 0 0}.tf-page-title.style-2{padding:24px 0 15px}.tf-page-title .breadcrumbs{margin-bottom:43px;width:100%;display:flex;display:-webkit-box;display:-moz-box;display:-ms-flexbox;display:-webkit-flex;align-items:center;justify-content:left}.tf-page-title .breadcrumbs li{font-weight:700;display:inline-block;padding:0 0 0 33px;color:var(--primary-color3);position:relative}.tf-page-title .breadcrumbs li a{font-weight:700;position:relative;color:var(--primary-color8)}.tf-page-title .breadcrumbs li::after{content:"";font-family:"Font Awesome 5 Pro";position:absolute;color:#8a8aa0;left:15px;top:0}.tf-page-title .breadcrumbs li:first-child{padding:0}.tf-page-title .breadcrumbs li:first-child::after{display:none}.tf-page-title .page-title-heading{color:var(--primary-color5)}.tf-page-title.style-2 .breadcrumbs{margin-bottom:0}